import _ from "lodash"
var cu = window.cu;


var ub = {

    ctx: {
        payload: {
            tpls: {},
            registerTpl: function (args, result, master) {

                result.obj.uid = cu.uids.run();

                if (result.obj.mold.props) result.obj.mold.props.tplId = result.obj.uid;
                //this.tpls.push(args.path);

                this.tpls[args.path.join(".")] = result.obj.uid;
            }
        },

        tb: {
            //parseHtml: cu.parseStringIntoComp
        }
    },

    run: function (obj, attrs) {
        let oldPayload = this.ctx.payload;

        this.ctx.payload.attrs = attrs;

        let mold = this.it(obj, []);

        this.ctx.payload = oldPayload;

        return mold;
    },


    it: function (obj, path) {
        let oldPayload = null;
        let result = {
            oldPayload: null,

            obj: obj,
            sub: obj,

            master: this,
            path: path,
            values: obj,
            ops: []
        }

        this.prepare(result);

        if ( _.isObject ( result.sub ))
        for (var i in result.sub) {
            let ret = this.it(result.sub[i], path.concat(i));
            result.values[i] = ret;
        }

        return this.integrate(result)
    },

    addPayload: function (result, obj) {
        if (!result.oldPayload) {
            result.oldPayload = this.ctx.payload;
            this.ctx.payload = Object.create(this.ctx.payload);
        }
        Object.assign(this.ctx.payload, obj);
    },

    prepare: function (result) {
        let checks = {
            isString: result => result.obj && result.obj.trim,
            isUnparsedTpl: result => result.obj && result.obj.html && !result.obj.mold,
            isCollection: result => result.obj && result.obj.isCollection,
            isObjTpl: result => result.obj && result.obj.VALUE
        }

        let cases = {
            "isString": (result) => {
                result.values = result.obj = { html: result.obj };
                result.sub = [];
                return "applied";
            },
            "isUnparsedTpl": (result) => {
                result.sub = [];
                let obj = Object.assign({}, result.obj, {
                    mold: this.ctx.tb.parseHtml.run(result.obj.html, this.ctx.payload.attrs)
                })

                result.values = result.obj = obj;

                result.ops.push({ name: "registerTpl", path: result.path })
                return "applied";
            },
            "isCollection": (result) => {
                let sub = {};
                for (var i in result.obj) {
                    if (i == "isCollection") continue;
                    sub[i] = { VALUE: result.obj[i] }
                }
                result.values = {};
                result.sub = sub;
                return "applied";
            },
            "isObjTpl": (result) => {
                result.sub = [];
                let obj = Object.assign({}, result.obj, {
                    mold: this.ctx.tb.parseObj.run(result.obj.VALUE, this.ctx.payload.attrs)
                })

                result.values = result.obj = obj;

                result.ops.push({ name: "registerTpl", path: result.path })
                return "applied";
            },
        }

        let hasChanges = true;
        let applied = false;
        let count = 0;
        while (hasChanges && count < 5) {
            hasChanges = false;
            count++;
            for (var i in cases) {
                let _check = checks[i];
                if (!_check(result)) continue;

                let _case = cases[i];
                let ret = _case(result);
                if (ret == "applied") {
                    hasChanges = true;
                    applied = true;
                    break;
                }
            }
        }
    },

    integrate: function (result) {
        if (result.ops.length) {
            for (var i in result.ops) {
                let op = result.ops[i];
                this.ctx.payload[op.name](op, result, this)
            }
        };

        if (result.oldPayload) this.ctx.payload = result.oldPayload;

        return result.values;
    }
}

