import _ from "lodash"
import "../libs/dataManager";

var cu = window.cu;

cu.DataManager = window.DataManager;

for (var i in cu) {
    if (cu[i].init) cu[i].init();
}



cu.tests = {
    wallet: {
        test: {},
        pins: {},
        pending: []
    },
    pin: function (name, value) {
        this.wallet.pins[name] = value;
    },
    get: function (name) {
        let value = _.get(this.wallet.pins, name);
        if (!value) value = _.get(this.wallet.test, name);
        return value;
    }
}


for (var i in cu) {
    if (cu[i].test) {
        cu.tests.wallet.pending.push(i);

    }
}


let passes = 0;
while (passes < 5 && cu.tests.wallet.pending.length) {
    let i = cu.tests.wallet.pending.shift();
    passes++;
    let result = {};
    let ret;
    try {
        ret = cu[i].test(result);
    } catch (e) {
        console.log("error when running ", i, e)
    }

    if (ret == "pending") {
        cu.tests.wallet.pending.push(i)
        continue;
    }
    console.group("test for ", i)

    cu.tests.wallet.test[i] = result;

    if (ret) console.log(ret)
    if (Object.keys(result)) {
        for (var i in result) console.log(i, result[i])
    }
    console.groupEnd();
}

if (cu.tests.wallet.pending.length)
    console.error("error with tests : ")


