import _ from "lodash"
import faker from "faker"
import { types } from "util";
var cu = window.cu;

var type = {}


type.dispatch = {
    getNode: function (item, values, result, ctx) {
        let nodeType = values.type;
        if (!nodeType) {
            if (values.tpl) nodeType = "reactNode"

            else if (values.node) nodeType = "dataNode"
        }

        if (nodeType && type[nodeType]) {
            return type[nodeType].getNode(item, values, result, ctx)
        }
        else result.isReady = false;
    }
}

type.tpl = {
    getNode: function (item, values, result, ctx) {
        delete values.type;
        let tpl = cu.tplObjParse.run(values);
        result.viewAs = "object";
        return tpl;
    }
}

type.dataNode = {
    getNode: function (item, values, result, ctx) {
        return item.node;
    }
}

type.reactNode = {
    getNode: function (item, values, result, ctx) {

        let tpl = values.tpl;
        if (!tpl || !tpl.mold) {
            result.isReady = false;
            return;
        }

        let neededValues = [];
        let node = cu.reactAssembler.run(tpl, values, (data, path, op) => {
            let value = _.get(data, path);
            if (value !== undefined) return value;
            neededValues.push(path);
            return "dummyvalue"
        });

        if (neededValues.length) {
            node = [neededValues, node];
        }
        result.viewAs = "reactComponent"

        result.cues = { pins: tpl.mold.pins }

        return node;

    }

}

type.mirror = {

    description: "",


    getNode: function (item, values, result, ctx) {


        let model = ctx.data.items[item.model];
        let data = values.data;

        let newItem = _.cloneDeep(model);

        if (!result.bubbles) result.bubbles = {};
        if (!result.cache) result.cache = { data: {} };

        if (!_.isMatch(result.cache.data, data)) {
            result.cache.data = data;

            for (var i in newItem) {
                if (i != "tpl" && newItem[i].id && data[i]) {
                    if (result.bubbles[i]) {
                        ctx.master.mergeItem(result.bubbles[i].id, { data: data[i] })
                    }
                }
            }
        }

        for (var i in newItem) {
            if (i != "tpl" && newItem[i].id && data[i]) {

                if (result.bubbles[i]) {
                    newItem[i] = { id: result.bubbles[i].id }
                }
                else {
                    let newId = ctx.master.addItem({ type: "mirror", data: data[i], model: newItem[i].id })
                    result.bubbles[i] = { id: newId, model: newItem[i].id };
                }

                newItem[i] = { id: result.bubbles[i].id }
            }
            else if (data[i]) {
                newItem[i] = data[i]
            }
        }

        newItem.id = item.id;

        let newValues = ctx.master.isCompleteValues(newItem);

        if (!newValues) {
            result.isReady = false;
            return;
        }

        result.mirror = {};
        ctx.master.evalItem(item, item.id, newValues, result.mirror)
        if (result.mirror.viewAs) result.viewAs = result.mirror.viewAs;
        return result.mirror.node;
    }
}

