import React, { Component } from 'react';
import * as d3 from "d3";
import * as webcola from "webcola";
import $ from "jquery"
import vis from "vis"


export default class TreeMap extends Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef();

    this.state = {
      id: "salut"
    }
  }

  componentDidMount() {
    this.makeTree();

    this.drawTree(this.props.graph)

  }
  makeTree() {
    let node = this.ref.current;

    this.nodes = new vis.DataSet();
    this.edges = new vis.DataSet();
    var options = {
      layout: {
        randomSeed: 0,
        improvedLayout: true,
        hierarchical: false

      },
      autoResize: true,

      "nodes": {
        "borderWidth": 1,
        "borderWidthSelected": 2,
        "color": {
          "border": "rgba(33,33,33,1)",
          "background": "hsl( 91.49505396307129,100% ,41.20740124521036%  )",
        },
        "font": {
          "color": "rgba(33,33,33,1)",
          "size": 12,
          "strokeWidth": 1
        },

        "shape": "box",
        "shapeProperties": {
          "borderRadius": 2
        },
      },
      "edges": {

      },

      physics: {
        stabilization: {
          onlyDynamicEdges: true
        },
        adaptiveTimestep: true,
      },

    };
    this.network = new vis.Network(node, { nodes: this.nodes, edges: this.edges }, options);

    this.network.on("click", (params) => {
      console.log("click", params);

      params.event.stopPropagation();
      params.event.preventDefault();

      let id = this.network.getNodeAt(params.pointer.DOM);
      if (id && this.props.onClick) this.props.onClick(id)
      else {
        $("#order").blur();
      }
      //console.log('click event, getNodeAt returns: ' + this.getNodeAt(params.pointer.DOM), params);
    });


  }

  drawTree(graph) {

    var options = {
      layout: {
        randomSeed: 0,
        improvedLayout: true,
        hierarchical: false

      },
      autoResize: true,

      "nodes": {
        "borderWidth": 1,
        "borderWidthSelected": 2,
        "color": {
          "border": graph.colors.main,
          "background": graph.colors.pastel,
        },
        "font": {
          "color": "rgba(33,33,33,1)",
          "size": 14,
          "strokeWidth": 1
        },

        "shape": "box",
        "shapeProperties": {
          "borderRadius": 2
        },
      },
      "edges": {

      },

      physics: {
        stabilization: {
          onlyDynamicEdges: true
        },
        adaptiveTimestep: true,
      },

    };

    this.network.setOptions( options)

    let h = $(this.ref.current).height();
    let w = $(this.ref.current).width();

    this.nodes.update(graph.nodes);
    this.edges.clear();
    this.edges.add(graph.edges);

  }


  componentWillReceiveProps(newprops) {
    this.drawTree(newprops.graph)
  }

  render() {
    const fn = function () {
      $("#order").focus();
    }

    return <div onClick={fn} ref={this.ref} id={"#" + this.props.id} style={{ height: "100%", width: "100%" }}></div>
  }

}
