
import React, { Component } from 'react';

class MyInput extends Component {

    constructor(props) {
      super(props);

      this.state = {
        value : ""
      }

      this.onChange = this.onChange.bind( this );
    }

    onChange ( evt ) {
      this.setState( {value : evt.target.value});

      if ( this.props.onChange ) this.props.onChange( evt );
    }
  

    componentWillReceiveProps(nextProps) {
      if ( nextProps.value!==undefined && nextProps.value.text!==undefined) this.setState({value : nextProps.value.text})
    }

    render () {

      let props = Object.assign({}, this.props, {value : this.state.value, onChange : this.onChange })
      
      return React.createElement("input", props, null);
    }
  }

  export default MyInput;