import _ from "lodash"
var cu = window.cu;


var ub = {

    ctx: {
        tb: {}
    },

    run: function (model, data, getter) {
        model = _.cloneDeep(model);
        let obj = model.mold.tpl;

        this.applyOps(model.mold.ops, obj, data, getter);


        if (obj.props)
            obj.props.tplid = model.uid;


        return obj;
    },

    applyOps: function (ops, obj, data, getter) {
        let get = getter || _.get;

        ops.sort( (a,b) => {
            if ( a.stage != b.stage) return a.stage - b.stage;

            return ( b.path.length - a.path.length )
        } )

        let newOps = [];
        for (var i = 0; i < ops.length; i++) {

            let op = ops[i];

            if (op.type == "value") {
                let value = get(data, op.action, op, newOps);
                _.set(obj, op.path, value)
            }
            else if (op.type == "computedValue") {
                let value = op.value;
                _.set(obj, op.path, value)
            }
            else if (op.type == "concat") {
                let value = get(obj, op.path, op, newOps).join("");
                _.set(obj, op.path, value)
            }
            else if (op.type == "pathSet") {
                let value = get(obj, op.path, op, newOps);

                let myPath = op.path.slice( 0, op.path.length - 1 );
                myPath.push( value.PATH )
                _.unset( obj, op.path )
                _.set( obj, myPath.join("."), value.VALUE );
            }
            else if (op.type == "makeFunction") {
                let body = get(obj, op.path, op, newOps).join("");
                try {
                    let fn;
                    eval("fn=" + body)

                    _.set(obj, op.path, fn)
                }
                catch (e) {
                    console.error ( "makeFunction::" + body)
                }
            }
        }

        if (newOps.length)
            this.applyOps(newOps, obj, data, getter)
    },


}


