import _ from "lodash"
import faker from "faker"
var cu = window.cu;



var ub = {

    ctx: {
    },


    addItem : function ( obj ) {
        let id = this.getId();
        obj.id = id;
        this.mergeItem( id, obj );
        return id;
    },

    getId : function ( ) {
        this.ctx.data.state.nextId++;

        let id = "bubble_" + this.ctx.data.state.nextId;

        return id;
    },
    mergeItem: function (id, value) {
        let ctx = this.ctx;
        let result = ctx.data.result[id];


        ctx.changed = {};

        this.appendTo(id, value)

        let minStep;
        let results = ctx.data.result;
        let changed = ctx.changed;

        console.log( ctx.data );
        let versions = ctx.data.versions;

        for ( var i in changed ) {
            if ( !versions[i] ) versions[i] = 0;
            versions[i]++;
            let result = results[ i ];
            if ( !result ) continue;
            if ( !result.isReady ) continue;
            if ( minStep == undefined ) minStep = result.completedAt;
            if ( minStep > result.completedAt ) minStep = result.completedAt;
        }

        if (minStep !== undefined) {
            this.rewindTo(minStep)
        }


        if ( this.ctx.tb.onchanged)
            this.ctx.tb.onchanged( changed )
        
        return minStep;
    },

    rewindTo: function (step) {
        let ctx = this.ctx;
        for (var id in ctx.data.result) {
            let result = ctx.data.result[id];
            if (result.isReady && (result.completedAt >= step || result.createdAt > step)) {
                result.isReady = false;
                result.completedAt = 0;
            }
        }

        ctx.data.step = step;
    },

    appendTo: function (id, values) {
        let ctx = this.ctx;
        let item = ctx.data.items[id];
        let hasChanges = false;

        if (!item) {
            ctx.data.step++;
            ctx.data.items[id] = item = {}
            ctx.data.result[id] = { createdAt: ctx.data.step }
            hasChanges = true;
        }

        for (var slot in values) {
            let value = values[slot];
            if ( value == "REMOVE" && item[slot] !== undefined ) {
                delete item[slot];
                hasChanges = true;
                continue;
            }
            if ( value === null && item[slot] !== undefined ) {
                delete item[slot]
                hasChanges = true;
                continue;

            }

            if ( !value ) continue;

            let bubbleid = item[slot] ? item[slot].id : null;
            if (_.isString(value) && value.charAt(0) == "#") {
                value = {id : value.substring(1) };
            }

            if ( value.id !== undefined) {
                if ( value.id == "#"  || value.id=="" ) value.id = null;
                value.id = value.id || bubbleid || this.getId();
                item[slot] = { id: value.id };
            }


            if (value.id && _.isObject(value)) {
                if ( value.id != bubbleid ) hasChanges = true;
                this.appendTo(value.id, value)
            }

            else if ( _.isObject( value ) && !_.isMatch(item[slot], value )) {
                hasChanges = true;
                item[slot] = value;
            }
            else if ( item[slot]!=value) {
                hasChanges = true;
                item[slot] = value;
            }

        }

        if( hasChanges ) {
            if ( item.version ) {
                item.version = item.version ? item.version+1 : 1;
            }
            this.ctx.changed[ id ] =true;
        }
    },

    evalItems: function (count) {
        this.ctx.affected = {};
        this._evalItems( count );

        if ( this.ctx.tb.onchanged)
            this.ctx.tb.onchanged( this.ctx.affected )
        
    },

    _evalItems : function ( count ) {

        if (count<=0) return;

        let ctx = this.ctx;

        let hasChanges = false;

        for (var id in ctx.data.items) {
            let item = ctx.data.items[id];
            let result = ctx.data.result[id];

            if (result && result.isReady) continue;
            let values = this.isCompleteValues(item, id);
            if (values) {
                if (!ctx.data.result[id]) ctx.data.result[id] = {};

                this.evalItem(item, id, values, ctx.data.result[id]);
                if ( ctx.data.result[id].isReady )
                    hasChanges = true;
            }
        }


        if (hasChanges) this._evalItems(count - 1);
    },

    evalItem: function (item, id, values, result) {
        let ctx = this.ctx;

        result.isReady = true;
        result.values = values;

        let node = this.ctx.tb.getNode(item, values, result, ctx);
        if ( node ) 
            result.node = node;

        if ( result.isReady ) {
            ctx.data.step++;
            result.completedAt = ctx.data.step;
            this.ctx.affected[ id ] = true;
        }

    },

    isCompleteValues(item) {
        //if (!this.ctx.tb.checkIfComplete(item)) return false;

        let ctx = this.ctx;

        let values = {};

        let bellow = [];

        for (var slot in item) {
            let value = item[slot];
            if (value.id) {
                let bubble = ctx.data.result[value.id];

                if (!bubble || !bubble.isReady) return false;

                let node = bubble.node;

                if ( item[slot+"_path"]) {
                    node = _.get( node, item[slot+"_path"] )
                }

                values[slot] = node;
            }
            else values[slot] = value
        }

        return values;
    },

    init: function (ctx) {
        let item = Object.create(this);
        item.ctx = ctx;

        _.defaultsDeep(ctx, {
            data : {
                items : {},
                result : {},
                step : 1,
                versions : {},
                trails : {},
                rels : {
                    bellow : {},
                    above  : {},
                    uses   : {}
                },

                state : {
                    nextId : 8
                }
            },  
            tb: {
                checkIfComplete: (item) => item.type || item.tpl || item.node,
                getNode: (item, values, result) => { return { val: values, x: item.tpl } }
            }
        })

        ctx.master = item; 
        return item;
    },

    evaluateflow : function () {
        this.evalItems(5);
    },

    getVersion : function ( id ) {
        let item = this.ctx.data.items[ id ];

        if ( !item ) return null;
        return item.version;
    }
}

