import _ from "lodash"
import faker from "faker"
import js_beautify from "js-beautify"
var cu = window.cu;



var ub = {

    ctx: {
        nextUID : 0
    },

    run : function (name ) {
        return (name || "uid" ) +"_"+ this.ctx.nextUID++;
    },


    makestr : function ( obj, indent ) {
        if ( _.isFunction( obj ) ) return obj.toString();
        if ( _.isString( obj ) ) {
 
            let usequouts = '"';
            if ( obj.indexOf( "\n") != -1 ) usequouts = "`";
            if ( obj.indexOf('"') != -1 ) usequouts = "`";
            
            if (usequouts == "`") {
                obj = js_beautify.html( obj, { 
                    indent_size : 2,
                    indent_level : indent + 1,
                    preserve_newlines : true,
                    max_preserve_newlines : 0,
                    indent_inner_html: true,
                } )

                obj = obj.trimStart();
            }
            return usequouts + obj + usequouts
        }
        if ( _.isArray(obj)) {
            let lines = [];
            for ( var i= 0 ;i <obj.length;i ++ ) {
                let value = this.makestr( obj[i] , indent + 1);

                lines.push( value)
            }

            return "[" + lines.join(",\n") + "]";
        }

        if ( _.isObjectLike(obj)) {
            let lines = [];
            let multiLine = false;
            for ( var i in obj ) {
                let slot = i;
                let value = this.makestr( obj[i], indent + 1 );

                if ( !slot.match( /^[a-z0-9$_]+$/) ) slot = '"' + slot + '"'

                if ( value.indexOf("\n") != -1 ) multiLine = false;
                lines.push( slot + ":" + value )
            }

            return "{" + lines.join(",") + "}"
        }
        
        if ( _.isNil( obj )) return "null"

        return obj.toString();
    },

    beautify : function ( str ) {
        return js_beautify.js_beautify( str, {
            indent_size :2 ,
            indent_level : -1, 
            max_preserve_newlines : 1,
            preserve_newlines : true
        } )
    }
}

