import _ from "lodash"
import $ from "jquery"

var cu = window.cu;


var ub = {

    ctx: {
        tb: {},

        wallet: {}
    },

    run: function (str) {
        let mold = str;
        mold = this.ctx.tb.parser.run(mold, ["comp"]);

        return mold;
    },

    parseTpl: function (obj) {
        let mold = this.ctx.tb.parser.run(obj, ["comp"]);
        return mold;
    },

    renderTpl: function (mold, data, getter) {
        return this.ctx.tb.reactAssembler.run(mold, data, getter)
    },

    assembleTpl: function (mold, data) {
        if (_.isString(mold)) mold = _.get(this.ctx.payload.molds, mold);
        return this.ctx.tb.reactAssembler.run(mold, data, (data, path) => {
            let value = _.get( data, path );
            if ( value.node ) return value.node;
            return value;
        })
    },


    iterateBubbles: function (bubbles, count ) {
        if ( count == undefined ) count = 0;

        if ( count && count > 5 ) return;

        bubbles = bubbles || this.ctx.payload.bubbles;

        let changed = false;
        for (var i = 0; i< bubbles.length; i++ ) {
            let bubble = bubbles[i];

            if ( bubble.isReady ) continue;


            let shouldAssemble = true;

            for (var j in bubble) {
                if (bubble[j].tpl) {
                    if ( !bubble[j].isRegistered ) {
                        bubble[j].isRegistered = true;
                        this.addBubble( bubble[j] )
                    }
                    if (!bubble[j].isReady) {
                        shouldAssemble = false;
                        continue;
                    }
                }
            }

            if (shouldAssemble) {
                let node = ub.assembleTpl(bubble.tpl, bubble);
                bubble.node = node;
                bubble.isReady = true;
                bubble.computedAtStep = this.ctx.payload.step++;
                changed = true;
            }
        }
        if ( changed)
            this.iterateBubbles( bubbles, count+1)

    },

    addBubble: function (bubble) {
        bubble.uid = cu.uids.run("bubble");
        bubble.addedAtStep = this.ctx.payload.step;
        this.ctx.payload.bubbles.push(bubble);
        //this.iterateBubbles();
    },

    start: function (molds) {
        this.ctx.payload = {
            bubbles: [],
            molds: molds,
            step : 0,
        }
    }

}


