var TimerManager = {
    init : function ( DataManager ) {
      var item = Object.create( this );

      item.DataManager = DataManager;
      item.wallet = {timers : {} }
      return item;
    },
    wallet: { timers: {} },
  
    start: function (name, action, delay) {
      if (this.wallet.timers[name]) window.clearTimeout(this.wallet.timers[name]);
  
      var timer = window.setTimeout(() => {
        this.cancel( name );
        action(name, action, delay);
      }, delay)
  
      this.wallet.timers[name] = timer;

      let date = new Date();
      this.DataManager.set("TimerManager.timer."  + name , { start : date.getTime(), end : date.getTime() + delay })
    },
  
    cancel: function (name) {
      if (this.wallet.timers[name]) window.clearTimeout(this.wallet.timers[name]);
      delete this.wallet.timers[name]
      this.DataManager.set("TimerManager.timer."  + name , "")

    }
  }

  export {TimerManager};