import _ from "lodash"
import faker from "faker"
import chroma from "chroma-js"
var cu = window.cu;



var ub = {

  getRandomFn: function (str) {
    var seed = 0;
    for (var i = 0 ; i <str.length; i++ ) {
      seed += str.charCodeAt(i) * i
    }
    function getRandom() {
      var x = Math.sin(seed++) * 10000;
      return x - Math.floor(x);
    }

    return getRandom;

  },

  getBrand: function (uid) {
    let randomFn = this.getRandomFn(uid);

    let baseBrand = {
      mainColor: "#007abc",
      complementColor: ""
    }

    let brand = {
      mainColor_hsl: this.getColor(randomFn(), baseBrand.mainColor),
      complementColor_hsl: this.getColor(randomFn(), baseBrand.mainColor),
    }

    brand.mainColor = "hsl( " + brand.mainColor_hsl[0] + "," + brand.mainColor_hsl[1] + "% ," + (brand.mainColor_hsl[2]) + "%  )"
    brand.complementColor = "hsl( " + brand.complementColor_hsl[0] + "," + brand.complementColor_hsl[1] + "% ," + (brand.complementColor_hsl[2]) + "%  )"

    brand.fadedColor = "hsla( " + brand.mainColor_hsl[0] + "," + 10 + "% ," + (brand.mainColor_hsl[2]) + "%, 1 )"
    brand.pastelColor = "hsla( " + brand.mainColor_hsl[0] + "," + 50 + "% ," + (80) + "%, 1 )"

    return brand;
  },

  getColor: function (seed, baseColor) {

    baseColor = baseColor || "#007abc";
    let hue = seed * 360;
    let color = chroma("#007abc").set("hsi.h", hue).hsl();

    color[1] = color[1] * 100;
    color[2] = color[2] * 100;

    return color;
  },
  get: function (seed, count) {

    var str = seed; //faker.lorem.sentence(10);
    var seed = 0;
    for (var i= 0; i<str.length; i++) {
      seed += str.charCodeAt(i) * i
    }
    function getRandom() {
      var x = Math.sin(seed++) * 10000;
      return x - Math.floor(x);
    }

    count = Math.max(2, Math.floor((getRandom() * 5)))



    let scale = 50 / count;

    let color = this.getColor(getRandom());

    let pointsStar = [
      [0, 0, 0.5, 0.5, 1, 0],
      [0, 0, 0.5, 0.5, 0, 1],
      [1, 1, 0.5, 0.5, 0, 1],
      [1, 1, 0.5, 0.5, 1, 0],
    ]



    let polies = [];
    for (var i = 0; i < count; i++) {
      for (var j = 0; j < count; j++) {
        let lum = 10; //points[i][j];
        let x = i * scale;
        let y = j * scale;
        let poly = pointsStar.map((poligonPoints) => {
          let col = "hsl( " + color[0] + "," + color[1] + "% ," + (color[2]) + "%  )";
          let classname = Math.round(getRandom() * 5);
          let points = poligonPoints.map((value, i) => {
            return value * scale + (i % 2 ? x : y)
          })
          polies.push(`<polygon
                  class='poligon poligon_${classname}'
                  points="` + points.join(" ") + `" stroke="transparent" fill="transparent" 
                  style=" strokeWidth: ` + (1 / scale) + `;
                  fill: `+ col + `;
                  opacity : `+ getRandom() + `; 
                  " />`)

        })
      }
    }

    return "<svg style='position :relative;width: inherit; height: inherit;'> " + polies.join("") + "</svg>"


  }
}

