import _ from "lodash"
import faker from "faker"
import PouchDB from "pouchdb"
import PouchDBFind from 'pouchdb-find';
import PouchDBQuickSearch from 'pouchdb-quick-search';

PouchDB.plugin(PouchDBFind);
PouchDB.plugin(PouchDBQuickSearch);

var cu = window.cu;


var ub = {

    ctx: {},

    serialize : function( obj ) {
        let item = _.cloneDeepWith( obj, function ( value ) {
            if ( _.isFunction( value ) ) return { TYPE : "FN", body : value.toString() };
        })

        if ( !item.createdAt ) item.createdAt = Date.now();

        return item;
    },

    deserialize : function ( obj ) {
        let item = _.cloneDeepWith( obj, function ( value ) {
            if ( value && value.TYPE == "FN" ) {
                let fn;
                try  { 
                    eval( "fn=" + value.body )
                } catch (e ) {

                }
                return fn;
            } 
        })

        return item;
    },

    save: function (value, id) {
        var db = this.ctx.db;

        let item = this.serialize( value );
        if (!item.uid) item.uid = id || item.id || this.generateUid();
        if (!item._id) item._id = item.uid;
  
        console.log( "item", item)
        return db.get(item._id)
          .then((doc) => {
            let rev = doc.rev;
            let _rev = doc._rev;

            item.rev = rev;
            item._rev = _rev;
            return db.put(item).then( ( i)=> {
            })
          })
          .catch(err => {
            return db.put(item);
          })



    },



    load: function (id) {
        return this.ctx.db.get(id).catch( e => {
            return null
        })
        .then ( item => {
            if ( item ) item = this.deserialize( item );
            return item;
        })
    },


    generateUid : function ( ) {
        this.cfg.state.nextId++;
        return this.cfg.state.prefix + "_" + this.cfg.state.nextId;
    },

    init: function (name) {
        let ctx = {
            db: new PouchDB(name, {auto_compaction: true}),
            state: {
                prefix: new Date().getTime().toString() + "_" + Math.random().toString(),
                nextId: 0,
                id : "state"
            }
        }

        let item = Object.create(this);

        item.ctx = ctx;

        item.load("state").then(state => {
            if ( state ) item.ctx.state = state
            else item.save( item.ctx.state )
        })

        return item;
    },

    destroy : function ( ) {
        this.ctx.db.destroy( ) 
            .then ( () => {

                alert( "done cleanup")
            })
    },

    loadfilter : function ( selector, count, sort ) {
        return this.ctx.db.find( { selector });
    },

    partialTextSearch : function ( word ) { 
        return this.ctx.db.query( (doc, emit)=> {
            if ( !doc.text ) return;
            let tokens = doc.text.split( /\s+/g );
            tokens.filter( word => word.length > 2 ).forEach(element => {
                emit( element, { type : doc.type, map : doc.mapId , parent : doc.parentId ,test : "salut"} )
            });
        }, {startKey : word, endKey : word + "\0"})
    },

    searchText : function ( text ) {
        return this.ctx.db.search( {
            query: text ,
            fields: ['title', 'text'],
            include_docs: true,
            highlighting: true
          })
    },

    loadFromIndex : function ( selector, sortField, sortDir, limit, fields ) {
        let db = this.ctx.db;
        //let fields = _.uniq( Object.keys(sort).concat(Object.keys( selector )) );

        if ( !selector[sortField] ) selector[sortField] = { $gt : null };
        let sort = [_.set( {}, sortField, sortDir ? "desc" : "asc")]
        return (
            db.createIndex(
                {index : {
                    fields : fields
                }}
            )
            .then ( (result)=>{
                return result.name
            })
            .then( (index )=>{
                //db.explain( {selector, limit, sort}) .then( (res)=> console.log( "explain", res ))
                return db.find( {selector , limit, sort}).then( result => result.docs)
            })
        ) 

    }



}

