import _ from "lodash"
var cu = window.cu;


var phase1 = {

    run: function (html) {
        html = html.trim();

        let nest = document.createElement('div');
        nest.innerHTML = html;

        let specs = [];

        for (var i = 0; i < nest.childNodes.length; i++) {
            let el = nest.childNodes[i];

            let spec = this._spec(el);

            specs.push(spec);
        }

        return specs.length == 1 ? specs[0] : specs;
    },


    _spec: function (el) {

        if (el.nodeType == 3) {
            return el.nodeValue
        }

        let props = {};
        for (var i = 0; i < el.attributes.length; i++) {
            let attr = el.attributes[i];
            let name = attr.name;
            if (name.indexOf("_") != -1) {
                name = name.replace(/\_(.)/g, (all, char) => { return char.toUpperCase() })
            }
            props[name] = attr.value;
        }

        if (props.class) {
            props.className = props.class;
            delete props.class;
        }

        if (props.style) {
            props.style = this._style(props.style);
        }


        let spec = {
          type: "node",
          componentName: el.nodeName.toLowerCase(),
          props: props,
          children: null,
      };

        if ( props.htmlSlot ) {
          props[ props.htmlSlot ] = el.innerHTML;
          return spec;
        }

        if (Object.keys(props) == 0) props = null;

        let children = []
        el.childNodes.forEach(child => {
            children.push(this._spec(child))
        })
        if (children.length == 0) children = null;
        spec.children = children;

        return spec;
    },

    _style: function (str) {
        str = str + ";";
        let reg = /\s*([^:\s]+)\s*\:\s*([^;]+)\s*\;+/g
        let css = {};
        let match;
        while (match = reg.exec(str)) {
            css[match[1]] = match[2]
        }

        return css;
    },

}

var phase2 = {
    run: function (obj , attrs ) {
      let payload = {
        ops: [],
        pins: {},
        parameters : {},
        attrs : {},
        watchAttrs : attrs || []
      }
  
      if ( _.isArray( obj ) )
        payload.tpl = obj.map(( obj, i)  => this.iterate(obj, [i], payload) )
      else 
        payload.tpl = this.iterate(obj, [], payload)

      payload.ops.sort ( (a,b) => a.stage - b.stage )
  
      return { ops: payload.ops, tpl: payload.tpl, pins : payload.pins , parameters : payload.parameters, attrs : payload.attrs}
    },
    iterate: function (obj, path, payload) {
      if (!obj) return;
  
      if (_.isString(obj)) {
        return this.opValues(obj, path, payload, false);
      }
  
      obj = this.handleObj(obj, path, payload);
  
      let childrenPath = path.concat(["children"])
      for (var i in obj.children) {
        obj.children[i] = this.iterate(obj.children[i], childrenPath.concat(i), payload)
      }
  
      let subPath = path.concat(["props"])
      for (var i in obj.props) {
        if (i == "style") continue;
        obj.props[i] = this.handleProp(obj.props[i], subPath.concat(i), obj, payload)
      }
  
      if (obj.props && obj.props.style) {
        let subPath = path.concat(["props", "style"])
        for (var i in obj.props.style) {
          obj.props.style[i] = this.handleProp(obj.props.style[i], subPath.concat(i), obj, payload)
        }
      }
  
      if (obj.props && obj.props.pin) {
        payload.pins[ obj.props.pin ] = path;
      }

      if (obj.props && obj.props.id) {
        payload.pins[ obj.props.id ] = path;
      }

      if ( obj.props )
        payload.watchAttrs.forEach( attr => {
            if ( !obj.props[attr] ) return ;
            if ( !payload.attrs[attr] ) payload.attrs[ attr ] = [];
            payload.attrs[ attr ].push({  path, value : obj.props[attr] } );
        })
  
      return obj;
    },
  
    handleObj: function (obj, path) {
      return obj;
    },
  
    handleProp: function (obj, path, parent, payload) {
      if (!_.isString(obj)) return obj;
  
      return this.opValues(obj, path, payload, true)
    },
    addOp: function (payload, path, op) {

        if ( !op.stage ) op.stage = 0;
      payload.parameters[ op.action ] = op.path;
      payload.ops.push(op)
    },
  
    prepareAction: function (item) {
      return item;
    },
    opValues: function (value, path, payload, concatOp) {
      if (!_.isString(value)) return value;
      if (value.indexOf("{") == -1) return value;
  
      let ops = payload.ops;
      let values = value.split(/(\{[^}\n\:\;]+)\}/g);
      values = values.map((item, i) => {
        if (item == "") return null
        if (item.charAt(0) !== "{") return item;
  
        item = item.substring(1);
        let action = this.prepareAction(item)
        return { type: "value", action: action };
      })
        .filter(item => item)
  
  
      if (values.length == 0) return null;
      else if (values.length == 1) {
        values = values[0];
        if (values.type) {
          values.path = path;
        }
        if ( _.isObject( values ))
          this.addOp(payload, values.path, values)
        return values;
      }
      else if (!concatOp) {
        values = values.map((item, i) => {
          if (item.type) {
            item.path = path.concat(i);
            item.index = i;
            item.stage = 0;
            this.addOp(payload, path, item);
  
            return item;
          }
          else return item;
        })
        return values;
      }
      else {
        values = values.map((item, i) => {
          if (item.type) {
            item.path = path.concat(i);
            item.index = i;
            item.concatParent = true;
            item.stage = 0;

            this.addOp(payload, path, item);
            return item;
          }
          else return item;
        })
        this.addOp(payload, path, { path: path, type: "concat", ops: values, stage : 1 });
        return values;
      }
  
    }
  
  }


var ub = {
    run : function ( html , attrs ) {
        var result = phase1.run( html );
        result = phase2.run( result, attrs );

        return result;
    }
}

var test = (result) => {
    result.output = ub.run( "<div>Salut {#test} </div>");
}

cu.parseStringIntoComp = {    
    //test,

    run: function (obj, attrs ) {
        return ub.run(obj, attrs )
    },
    def: `
        @obj string 
        transform it into componentsObjects
    `
}