import _ from "lodash"
import faker from "faker"
var cu = window.cu;


var actionsBundle = {}

actionsBundle.db = {

    saveItems : function ( op, v ) {

        let items;
        
        if ( _.isArray( op.items ) ) items =op.items;
        else if ( op.items.id ) items =  [op.items]
        else items =  Object.values( op.items );

        v.tb.db.saveItems( cu.uids.run() , items )
            .then( result => {
                console.log( "saveItems", result)
                v.actions.pinTo(op, result, v)

            })

        
        if (op.append)
        v.actions.appendById({ path: op.append, value: items }, v)


    },

    loadItem: function (op, v) {

        let promise;
        let loadedItems;

        if ( op.append && v.data[ op.append ]) {
            loadedItems =  v.data[ op.append ];
        }

        v.tb.db.loadItems([op.id], loadedItems ).then(item => {

            if (v.cancel) return;

            if (op.append)
                v.actions.appendById({ path: op.append, value: item }, v)


            if (op.pin)
                v.actions.setValue({ path: op.pin, value: item[0] }, v)

        })
    },

    loadItems: function (op, v) {
        let ids = _.isArray(op.ids) ? op.ids : Object.keys(op.ids);
        let loadedItems;

        if ( op.append && v.data[ op.append ]) {
            loadedItems =  v.data[ op.append ];
        }


        v.tb.db.loadItems(ids, loadedItems).then(items => {

            if (v.cancel) return;

            if (op.append) {
                v.actions.appendById({ path: op.append, value: items }, v)
            }
            if (op.pin)
                v.actions.setValue({ path: op.pin, value: items }, v)
        })
    },


    loadFromIndex : function ( op, v ) {
        v.tb.db.loadFromIndex( op ).then( items => {

            if (v.cancel) return;


            if (op.append) {
                v.actions.appendById({ path: op.append, value: items }, v)
            }
            if (op.pin)
                v.actions.setValue({ path: op.pin, value: items }, v)
        } )
    },

    searchItems: function (op, v) {
        let filter = op.filter;

        v.tb.db.loadFilter(filter).then(docs => {

            let items = docs.docs;

            if (v.cancel) return;



            if (op.append) {
                v.actions.appendById({ path: op.append, value: items }, v)
            }
            if (op.pin)
                v.actions.setValue({ path: op.pin, value: items }, v)
        })
    },


    searchText: function (op, v) {
        this.tb.db.searchText(op.text).then(items => {

            if (v.cancel) return;


            if (op.pin)
                v.actions.setValue({ path: op.pin, value: items }, v)
        })
    },

}
actionsBundle.core = {
    triggerRerun: function (val, v) {
        if (v.running) {
            v.shouldRerun = true;
        }
        else {
            v.actions.runAllOps({}, v);
        }
    },

    prepareOp: function (op, v) {
        let args = { src: {} };
        for (var i in op.input) {
            if (i == "type") args.type = op.input[i];
            else if (!_.isString(op.input[i])) args[i] = op.input[i]
            else {
                args.src[i] = op.input[i];
                let value = v.actions.getValue(op.input[i], v);
                if (value === undefined) {
                    if (!op.partial)
                        return null;
                    else {
                        if (!args.missing) args.missing = [];
                        args.missing.push(i);
                    }
                }
                else args[i] = value;
            }
        }
        for (var i in op) {
            if (i != "input")
                args[i] = op[i]
        }

        args.OP = op;

        return args;
    },
    runOp: function (op, v) {
        if (!op.uid) op.uid = v.actions.getUid("", v);

        if ( op.waitfor ) {

            let satisfied = true;

            for ( var i in op.waitfor ) {
                let value = v.actions.getValue( op.waitfor[i], v );
                if ( value === undefined ) return "notcomplete"
            }
        }

        let args = v.actions.prepareOp(op, v);
        if (args === null) {
            return "notcomplete";
        }

        if (args.name) args.pin = args.name;


        if ( args.partial && args.on && args.on.partial ) {
            if ( args.on.partial.value !== undefined ){
                v.actions.pinTo( args,args.on.partial.value, v );
                return;
            }
        }

        if (!v.actions[args.type]) {
            console.error({ v, args, op })
        }
        let value = v.actions[args.type](args, v);

        if (value === "notcomplete") return "notcomplete";
    },

    getUid: function (args, v) {
        v.master.state.nextId++;
        let id = "op_" + v.master.state.nextId;
        return id;
    },

    opIsDone: function (op, v) {
        if (op.partial) return false;
        if (!op.pin) return false;

        return _.get(v.data, op.pin) !== undefined;

    },
    runAllOps: function (args, v) {
        v.ncOps = [];

        v.running = true;
        v.shouldRerun = false;

        for (var i in v.ops) {
            let op = v.ops[i];

            let isDone = v.actions.opIsDone(op, v)
            if (!isDone) {
                let value = v.actions.runOp(op, v);

                if (value === "notcomplete") v.ncOps.push(op)
                else v.shouldRerun = true;
            }
        }

        v.ops = v.ncOps;

        v.running = false;

        if (v.shouldRerun) {
            v.shouldRerun = false;
            this.runAllOps(args, v)
        }

        else {
            if (v.ncOps.length == 0) {
                v.actions.checkGuards({ path: "DONE" }, v)
            }
            else {
                //console.log( "pending", {v})
            }
        }



    },

}

actionsBundle.values = {
    pinTo: function (args, value, v) {
        if (args.pin) {
            v.actions.setValue({ path: args.pin, value: value }, v)
        }
    },

    staticValue: function (args, v) {
        v.actions.pinTo(args, args.value, v)
    },

    getValue: function (path, v) {
        if (v.privat) return this.getFromPrivate( path, v );
        if (v.prefix) return this.getValueWithPrefix(path, v)
        return _.get(v.data, path);
    },

    getFromPrivate : function ( path, v  ) {
        let value = _.get( v.privat, path );
        if ( value != undefined ) return value;

        if (v.prefix) return this.getValueWithPrefix(path, v)

        return _.get(v.data, path);
    } ,

    getValueWithPrefix: function (path, v) {
        let value = _.get(v.data, v.prefix + "." + path);
        if (value === undefined) value = _.get(v.data, path);
        return value;
    },


    checkGuards: function (val, v) {

        var triggers = v.triggers[val.path];

        if (triggers) {

            if (!_.isArray(triggers)) triggers = [triggers];

            triggers.forEach(item => {
                if (_.isFunction(item))
                    item(val.path, val.value, val.isPartial, v)
                else if (item.run)
                    item.run(val.path, val.value, val.isPartial, v)

            })

        }

        if (v.guards) {
            var triggers = v.guards[val.path];

            if (triggers) {

                if (!_.isArray(triggers)) triggers = [triggers];

                triggers.forEach(item => {
                    if (_.isFunction(item))
                        item(val.path, val.value, val.isPartial, v)
                    else if (item.run)
                        item.run(val.path, val.value, val.isPartial, v)

                })
            }
        }

    },


    setValue: function (val, v) {
        let path = val.path;

        if (v.privat) {
            _.set( v.privat, path, val.value )
        }

        else {
            if (v.prefix) path = v.prefix + "." + val.path;

            _.set(v.data, path, val.value);
        }

        this.checkGuards(val, v)

        if (val.isPartial) return;

        v.actions.triggerRerun({}, v)


    },


    appendById: function (val, v) {
        let target = _.get(v.data, val.path);
        if (!target) {
            target = {};
            _.set(v.data, val.path, target);
        }

        let value = val.value;
        if ( value.id ) value = [value];

        for (var i in value) {
            if ( !value[i]) {
                continue;
            }
            let slot = value[i].id
            if ( !target[slot] )
            target[slot] = value[i];
        }


        if (val.isPartial) return;

        v.actions.triggerRerun({}, v)
    },
}

actionsBundle.generic = {
    log: function (val, v) {
        let obj = {};
        for (var i in val.OP.input) {
            obj[i] = val[i]
        }
        console.log("log", obj, v);
    },

    fn: function (op, v) {
        if (!op.fn) return;
        let result

        if (op.fn.run)
            result = op.fn.run(op, v);
        else
            result = op.fn(op, v);

        if (result === "notcomplete") return result;

        if (op.pin) {
            v.actions.setValue({ path: op.pin, value: result }, v)
        }
    },

    assembleObj: function (args, v) {
        let obj = {};

        for (var i in args.OP.input) {
            obj[i] = args[i]
        }

        if (args.partial && args.pin && args.missing) {

            if (!v.opState[args.uid]) v.opState[args.uid] = { missing: [] }


            obj.partial = true;

            if (_.isEqual(args.missing, v.opState[args.uid].missing)) {
                return "notcomplete";
            }
            else
                v.opState[args.uid].missing = args.missing;

            v.actions.setValue({ path: args.pin, value: obj, isPartial: args.missing ? "skipRerun" : false }, v)
        }

        else if (args.pin && !args.missing)
            v.actions.setValue({ path: args.pin, value: obj }, v)

        if (args.missing) return "notcomplete";

    },

}

actionsBundle.tpls = {
    listTemplate: function (args, v) {
        let list = [];

        for (var i in args.list) {
            let item = cu.moldAssembler.run(args.tpl, args.list[i]);
            list.push(item)
        }


        if (args.pin) {
            v.actions.setValue({ path: args.pin, value: list }, v)
        }

        return list;
    },

    useTpl: function (args, v) {
        let list = [];
        let isPartial = false;

        let item = cu.moldAssembler.run(args.tpl, args, function (data, path) {
            let value = _.get(data, path);
            if (value === undefined) value = _.get(v.data, path);

            if (value === undefined) {
                value = "";
                isPartial = true;
            }

            return value;
        });

        if (args.pin) {
            v.actions.setValue({ path: args.pin, value: item, isPartial: isPartial }, v)
        }


        if (args.append) {
            v.actions.appendById({ path: args.append, value: item }, v)
        }

        return item;
    },
}

var flowGuards = {

    makeTriggers: function (guards) {

        let triggers = {};

        for (var i in guards)
            triggers[i] = this.makeTrigger(i, guards[i])
        return triggers;
    },

    makeTrigger: function (index, guard) {
        let tb = guard.tb;
        let name = guard.name;
        let trigger = function (path, result, partial, v) {
            let triggerName = name || path;
            v.master.tb[tb].trigger(name, result, partial)
        }
        return trigger;
    }
}
var ub = {

    setup: function (opt) {
        let item = Object.create(this);

        item.ctx = {
            actions: {},
            opsCollection: opt.opsCollection || {},
            data: opt.data || {},
            triggers: {},
            valuesChain: {}
        };

        item.state = {
            nextId: 1,
            open: {},
            temp : []
        };

        item.tb = opt.tb || {};

        item.addTriggers(opt.triggers);

        item.addActionsCollection(actionsBundle);

        if (opt.actions)
            item.addActionsCollection(opt.actions);


        if (opt.ops)
            item.addMainOps(opt.ops)

        if (opt.guards)
            item.addTriggers(flowGuards.makeTriggers(opt.guards));

        if (opt.valuesChain)
            item.addValuesChains(opt.valuesChain)
        return item;
    },

    addValuesChains: function (chains) {
        for (var i in chains) {
            let chain = chains[i];
            if (_.isString(chain.unset)) chain.unset = chain.unset.split(",");

            this.ctx.valuesChain[i] = chain;
        }
    },
    addActionsCollection: function (collection, info) {

        for (var i in collection) {
            if (_.isString(collection[i]))
                this.fetchCollection(collection, i, collection[i])
        }

        for (var i in collection)
            this.addActions(collection[i], Object.assign({}, info, { slot: i }))
    },

    fetchCollection: function (target, name, path) {
        let values = _.cloneDeep(cu.DataManager.getValue(path));

        if (values.isCollection) {
            _.unset(target, name);
            delete values.isCollection;

            for (var i in values) {
                if (_.isString(values[i]))
                    this.fetchCollection(values, i, values[i])
            }
            _.merge(target, values)
        }
        else {
            target[i] = values;
        }
    },

    addActions: function (actions, info) {
        const getAction = function (action) {
            return function (args, v) {
                return action.run(args, v)
            }
        }
        for (var i in actions) {
            let action = actions[i];
            if (action.run) action = getAction(action)
            this.ctx.actions[i] = action
        }
    },

    addOps: function (name, ops) {
        this.ctx.opsCollection[name] = ops;
    },

    addMainOps: function (ops) {
        this.addOps("main", ops)
    },

    addTriggers: function (triggerCollection) {
        for (var i in triggerCollection) {
            let triggers = triggerCollection[i];
            if (!_.isArray(triggers)) triggers = [triggers];

            if (this.ctx.triggers[i]) triggers = this.ctx.triggers[i].concat(triggers);

            this.ctx.triggers[i] = triggers;

        }
    },

    run: function (opName, values, guards, extra ,  isTemp) {

        opName = opName || "main";

        if (values)
            this.changeData(values)

        let ops = this.ctx.opsCollection[opName];

        if (!ops) return;

        let v = this.getOpen(opName, isTemp);
        v.ops = ops;

        delete v.guards;
        if (guards) v.guards = guards;

        if ( !v.memo ) v.memo = {}


        _.merge( v, extra );
        
        if ( extra &&  extra.privat )
            v.privat = extra.privat
        else
            delete v.privat

        this.ctx.actions.runAllOps({}, v);
    },

    getOpen: function (name, isTemp ) {
        if (!isTemp && this.state.open[name]) return this.state.open[name];

        let v = {
            master: this,
            data: this.ctx.data,
            actions: this.ctx.actions,
            tb: this.tb,
            triggers: this.ctx.triggers
        }

        if ( !isTemp )
        this.state.open[name] = v;
        else 
            this.state.temp.push( v )

        return v;
    },

    cancel: function (name) {
        let open = this.state.open[name];

        open.cancel = true;

        delete this.state.open[name];
    },

    changeData: function (data) {
        for (var i in data) {
            if (this.ctx.valuesChain[i])
                this.applyValuesChain(i, data[i], this.ctx.valuesChain[i])
        }

        for (var i in data) {
            let path = i;
            let value = data[i];
            if (value === null)
                _.unset(this.ctx.data, path)
            else
                _.set(this.ctx.data, path, value)
        }
    },

    applyValuesChain: function (path, value, chain) {
        let unset = chain.unset
        if (unset)
            unset.forEach(slot => _.unset(this.ctx.data, slot))

        if ( chain.reset ) {
            for ( var i in chain.reset )
            this.ctx.data[i] = chain.reset[ i ].value 
        }
    }



}

