import _ from "lodash"
import faker from "faker"
var cu = window.cu;



var ub = {

    ctx: {
        tb: {},
        payload1: {
            tplsMapping: [
                "map",
                "topic",
                "item",
                "entry"
            ],
            defaultTpl: "item",
            usedGlobalStyles: {},


            addSubs: function (args, result, master) {
                result.obj.subs = result.values;
                //console.log( arguments );

                let obj = result.obj;
                let content = [];
                for (var i in result.values) {
                    let item = result.values[i];
                    if (item.nodes && item.nodes.card)
                        content.push(item.nodes.card)
                }

                if (content) {
                    result.obj.nodes.content = master.ctx.payload.useTemplate(obj.tpl + ".content", { subs: content });
                }

                let data = result.obj.nodes;
                result.obj.nodes.card = master.ctx.payload.useTemplate(obj.tpl + ".card", data)

            },

            useTemplate: function (tpl, data, skipGlobalStyle) {

                if (!skipGlobalStyle) {
                    let route = tpl.split(".");
                    let root = ub.ctx.wallet.molds;

                    for (var i = 0; i < route.length; i++) {
                        let step = route[i];
                        if (root[step].globalStyle) {
                            let id = root[step].globalStyle.uid;
                            this.usedGlobalStyles[id] = { parent: root[step] };
                        }
                        if (root[step])
                            root = root[step];
                        else break;
                    }
                }

                let mold = _.get(ub.ctx.wallet.molds, tpl); // .item.title;
                return ub.ctx.tb.reactAssembler.run(mold, data)
            },
            cases: [
                (result, master) => {

                    let isApplicable = !result.obj.data
                    if (!isApplicable) return;

                    let tpls = master.ctx.payload.tplsMapping;

                    let defaultTpl = tpls[result.obj.level] || master.ctx.payload.defaultTpl;


                    let obj = {
                        tpl: result.obj.tpl || defaultTpl,
                        data: result.obj.item,
                        level: result.obj.level,
                        nodes: {}
                    }

                    result.subs = result.obj.subs;
                    result.obj = obj;
                    result.ops.push({ name: "addSubs", args: "subs" })

                    return "applied"
                },

                (result, master) => {
                    let isApplicable = !result.obj.nodes.title
                    if (!isApplicable) return;

                    let obj = result.obj;

                    let mold = ub.ctx.wallet.molds.item.title;
                    let values = {
                        title: result.obj.data.text,
                        level: result.obj.level
                    }

                    result.obj.nodes.title = master.ctx.payload.useTemplate(obj.tpl + ".title", values);

                    return "applied"
                },


            ]

        },

    },

    getMolds: function (tpls) {
        let molds = this.ctx.tb.parser.run(tpls);
        return molds;
    },


    buildTpls: function (data) {
        let node = this.ctx.tb.iterator.run(data, { payload: this.ctx.payload1 });
        return node;
    },

    addGlobalStyles: function (nodes, gbs) {
        let styles = [];
        gbs = gbs || this.ctx.payload1.usedGlobalStyles


        var parent;

        let makeSelector = (path) => "[tplid='" + parent[path].uid + "']";

        let getter = (data, path) => {
            if (parent[path]) {
                return makeSelector(path)
            }
            else if (path == "*")

                return Object.keys(parent).filter(item => item != "globalStyle").map(item => makeSelector(item)).join(",")

            return _.get(data, path)
        }

        let data = { color: "red" }
        for (var i in gbs) {
            parent = gbs[i].parent;
            let style = ub.ctx.tb.reactAssembler.run(parent.globalStyle, data, getter)
            styles.push(style)
        }

        if (styles.length) {
            nodes = [nodes, styles];
        }
        return nodes;
    }
}

