DataManager.setCombined("ub.fnsub", {
  setup: function (opts) {

    _.merge(this, opts)

    this.fns = opts.fns || myUB;

    this.waiting = this.waiting || {};

    let setupRequired = false;

    if (this.old && this.old.subs) {
      for (var i in this.old.subs) {
        let sub = this.old.subs[i];
        this.makeUb(sub[0], sub[1], sub[2])
      }
      setupRequired = true;
      delete this.subs;
    }

    if (opts.ubs) {
      for (var i in opts.ubs) {
        let ub = opts.ubs[i];
        this.makeUb(ub.name, ub.path, ub.extra)
      }
      setupRequired = true;
    }

    if (setupRequired)
      this.setupAll();
  },


  detach: function () {
    let id = cu.uids.run();

    let name = "internal_" + id;

    this.makeUb(name, "ub.fnsub", {
      id: id,
      fns: {},
      parent: this
    });

    this.setupAll();


    let item = this.fns[name];

    this.fns[name].fns.fnsub = item;

    item.getFromParent("db")
    return item.fns;
  },


  getFromParent: function (names) {
    if (_.isString(names)) {
      names = [names]
    }

    for (var i in names) {
      let name = names[i] || i;
      let ub = this.parent.fns[name];

      this.fns[name] = ub;
    }
  },
  addUb: function (spec) {
    if (_.isArray(spec)) {
      spec.forEach(spec => {
        if (_.isString(spec) || !spec.name) this.getFromParent(spec)
        else this.makeUb(spec.name, spec.path, spec.extra)
      });
      this.setupAll();
      return;
    }
    this.makeUb(spec.name, spec.path, spec.extra);
    this.setupAll();

    return this.fns[spec.name];
  },
  addUbs: function (spec) {
    this.addUb(Object.values(spec));
  },
  setupAll: function () {
    let fns = this.fns;
    for (var i in fns) {
      if (fns[i].goSetup)
        this.setupUb(fns[i])
    }

    return true;
  },

  makeUb: function (name, path, extra, fns) {

    fns = fns || this.fns;
    let obj = _.isString(path) ? Object.create(DataManager.getValue(path)) : path;
    obj.goSetup = true;
    obj.extra = extra;
    obj.uid = fns[name] && fns[name].id ? fns[name].uid : (extra.id || cu.uids.run());


    if (fns[name]) _.merge(fns[name], obj)
    else
      fns[name] = obj;
  },

  setupUb: function (obj, fns) {
    fns = fns || this.fns;
    delete obj.goSetup;

    let values = {};
    for (var i in obj.extra) {
      let target = _.isString(obj.extra[i]) ? _.get(fns, obj.extra[i]) : obj.extra[i];
      if (!target) {
        target = { uid: cu.uids.run() };
        _.set(fns, obj.extra[i], target)
      }
      _.set(values, i, target)
    }

    if (obj.setup)
      obj.setup(values);

    obj.isReady = true;

    if (this.waiting[obj.uid]) {
      this.waiting[obj.uid].forEach(payload => {
        payload.resolve()
      })

      this.waiting[obj.uid] = [];
    }
  },

  onready: function (sub) {

    if (sub.isReady) return Promise.resolve();

    let payload = {};
    let promise = new Promise((resolve) => {
      payload.resolve = resolve;
    })

    if (!this.waiting[sub.uid]) this.waiting[sub.uid] = [];
    this.waiting[sub.uid].push(payload);

    return promise;
  }


});

DataManager.setCombined("ub.db", {
  setup: function () {
    this.db = cu.db.run("thisdb");
    this.timers = {};
  },

  destroy: function () {
    this.db.destroy();
  },
  request_save: function (id, src, delay) {
    if (this.timers[id]) window.clearTimeout(this.timers[id]);

    this.timers[id] = window.setTimeout(() => {
      this.saveItems(id, src)
    }, delay || 1000)
  },

  saveItems: function (id, src) {
    if (this.timers[id]) window.clearTimeout(this.timers[id]);

    let items = (_.isArray(src)) ? src : src.getItemsToSave();
    let promises = [];
    for (var i in items) {
      promises.push(this.db.save(items[i], items[i].id));
    }

    return Promise.all(promises)
  },

  loadItems: function (src, loadedItems) {
    let items = (_.isArray(src)) ? src : src.getItemsToSave();
    let promises = [];
    loadedItems = loadedItems || {};

    for (var i in items) {
      if (loadedItems[items[i]]) {
        promises.push(Promise.resolve(loadedItems[items[i]]))
      }
      else
        promises.push(this.db.load(items[i]))

    }

    return Promise.all(promises);
  },


  searchText: function (text) {
    return this.db.searchText(text)
  },

  loadFilter: function (filter, count, sort) {
    return this.db.loadfilter(filter, count, sort)
  },

  loadFromIndex: function (params) {
    let { selector, sortField, sortDir, count, index } = params;

    return this.db.loadFromIndex(selector, sortField, sortDir, count, index);
  }
})


