DataManager.setCombined("ub.actionshops", {
  setup: function () {
    this.shops = {};
    this.ub = myUB.fnsub.detach();

  },

  go: function (actionName, args, guards) {
    let actions = DataManager.getWallet("actionShop.actions");
    let action = actions[actionName];

    let path = action.path;
    let name = action.opName;
    //let guards = action.guards;

    let values = {};
    for (var i in action.args) {
      let slot = action.args[i];
      let val = _.get(args, i);
      if (val !== undefined)
        _.set(values, slot, val)
    }

    return this.run(path, name, values, guards)
  },
  run: function (path, name, values, guards) {
    let shop = this.get(path);

    return shop.run(name, values, guards);
  },
  get: function (path) {
    let shop;
    if (this.shops[path]) {
      shop = this.shops[path]
    }
    else {
      shop = this.createShop(path)
    }

    return shop;
  },

  createShop: function (path, extra) {
    let specs = DataManager.getValue(path);
    specs = _.cloneDeep(specs);

    specs.setup = cu.actionFlow.setupFlow;


    let view = {
      name: path,
      path: specs,
      extra: {
        "tb.db": "db",
      }
    };

    if (extra) {
      _.merge(view.extra, extra);
    }

    let item = this.ub.fnsub.addUb(view)

    this.shops[path] = item;

    return item;
  },

})



