DataManager.setCombined("actionsBundle.tree", {
  tree: {
    treeTemplate: {
      run: function (args, v) {
        let items = [];

        this.processNode(args.data, args.tpl, items);

        v.actions.pinTo(args, items, v);
      },
      processNode: function (data, tpl, v) {
        let item = cu.moldAssembler.run(tpl, data);

        for (var i in data.subs) {
          this.processNode(data.subs[i], tpl, v)
        }

        v.push(item);
        return item;
      }
    },
    makeTree: {
      run: function (args, v) {
        let result = this.makeNode(args, args.startId, { startNode: true }, 0);
        v.actions.pinTo(args, result, v);
        return result;
      },

      makeNode: function (v, id, info, level) {
        let subs = v.rels[id];
        let item = v.items[id];

        let result = { subs: {}, item, info, count: 0, level };

        if (!v.maxLevel || v.maxLevel > level)
          for (var i in subs) {
            result.subs[i] = this.makeNode(v, i, subs[i], level + 1);
            result.count++;
          }

        return result;
      }
    },

  }
})
