      iterateList: function (args, v) {
        let items = args.items;
        let index = 0;

        let cb = function (value) {
          let prom = new Promise((resolve) => {
            v.tb.generatePreview.run("main", value, {
              DONE: [
                (value) => {
                  resolve(value)
                }
              ]
            })
          })
          return prom;
        }

        let fn = function () {
          index++;
          if (index >= items.length) return;
          let value = items[index];
          return cb(value).then((result) => {
          }).then(fn);
        }

        fn();


      },
