DataManager.setCombined("actionshops.makeTreeMap", {
  data: {
    antId: "ant_2",

    tpls: {
      main: "<treemap graph={graph} on_click={actions.select} />"
    }
  },
  ops: [

    { name: "root", append: "items", input: { id: "antId" }, type: "loadItem" },
    { name: "subs", append: "items", input: { ids: "root.rels.allSubs" }, type: "loadItems" },
    { type: "log", input: { subs: "root.rels.allSubs" } },
    { type: "useTpl", name: "nodes", input: { tpl: "molds.main", graph: "graph" } },
    { type: "getNodesVis", name: "graph", input: { _s: "subs", startId: "root.id", items: "items", subs: "root.rels.sub", brand: "root.brand" } },
    { type: "getActions", name: "actions" },

  ],

  valuesChain: {
    "antId": {
      unset: "root,subs,graph,nodes"
    }
  },

  opsCollection: {
    setup: [
      { name: "molds", input: { tpls: "tpls" }, type: "makeMold" },
    ],
  },
  actions: {
    bundle: "actionsBundle.generics",
    items: {

      getActions: function (op, v) {

        var actions = cu.store.get(null, hapi, {
          runWithParam: {
            select: "select"
          }
        });


        v.actions.pinTo(op, actions, v)
      },
      getNodes: function (op, v) {
        let graph = {
          nodes: [],
          links: []
        }
        let fn = function (id, parentIndex, level) {
          if (level > 3) return;
          let index = graph.nodes.push({ name: op.items[id].text, radius: Math.max(5, 40 - level * 10) }) - 1;

          if (parentIndex !== null)
            graph.links.push({ target: index, source: parentIndex, value: 1 })
          for (var i in op.subs[id]) {
            fn(i, index, level + 1)
          }
        }

        fn(op.startId, null, 0)
        v.actions.pinTo(op, graph, v)
      },
      getNodesVis: function (op, v) {
        let graph = {
          nodes: [],
          edges: []
        }
        let fn = function (id, parentIndex, level, parentId) {
          if (level > 3) return;
          let nodeId = graph.nodes.length + 1;
          let index = graph.nodes.push({ id: id, label: op.items[id].text }); //name :  op.items[ id ].text ,radius : Math.max( 5, 40  - level * 10 ) } ) -1 ;

          if (parentIndex !== null)
            graph.edges.push({ to: id, from: parentId })//, value : 1})
          for (var i in op.subs[id]) {
            fn(i, index, level + 1, id)
          }
        }

        fn(op.startId, null, 0)

        graph.seed = 2;
        graph.colors = {
          main: op.brand.mainColor,
          pastel: op.brand.pastelColor
        }
        v.actions.pinTo(op, graph, v)
      }
    },
  }
});




