var hapi = {

  showMindMap: function (id) {
    DataManager.runOnLoader("antsView.formPanel.nodes", "change", { id: id })
    DataManager.set("workingant.viewState.path", "antsView.formPanel.nodes");
    DataManager.markChangeOn("workingant.viewState", false, true)

  },

  showhud: function () {
    let currentView = DataManager.getValue("workingant.viewState.path");

    currentView = currentView !== "antView.map.nodes" ? "map" : "bridge"

    this.goToCard(currentView);

    //DataManager.set("workingant.viewState.path", currentView);
  },
  placeorder: function (evt) {
    if (evt.which !== 13) return;

    flow.place();
    DataManager.set("workingant.data.input", { text: "" })
    flow.restart();
  },
  set: function (path, value) {
    DataManager.set(path, value)
  },
  parseinput: function () {

    let text = DataManager.getValue("workingant.data.input");

    if (_.isString(text)) {
      hapi.prepareMenu(text);
      flow.parse(text)
    }
  },

  prepareMenu: function (text) {
    const type = (type) => ({ type: type })
    const checkTokenMatch = (token, index, item) => {
      console.log( "item", item.hasTail , item.tokens[index])
      if (!item.tokens[index]) {
        console.log( item.hasTail )
        return item.hasTail;
      }
      if (item.tokens[index] != token) return false;
      return true;
    }

    const checkToken = (token, index, item) => {
      if (!item.tokens[index]) return { score: 1 };
      if (item.tokens[index].indexOf && item.tokens[index].indexOf(token) == 0) return { score: 100 };
      return { score: 1 };
    }

    let itemsSpecs = [
      {
        tokens: [".m"], 
        hasTail : true,
        actions: [
          {  },
          { scout: "maps", msg: "getting map #" }
        ]
      }
    ]

    let tokens = text.split(/\s+/);

    let match = itemsSpecs;

    for (var i = 0; i < tokens.length - 1; i++) {
      let token = tokens[i];
      let nextMatch = [];
      for (var j in match) {
        if (checkTokenMatch(token, i, match[j])) {
          nextMatch.push(match[j])
        }
      }

      match = nextMatch;
    }


    let finalMatch = [];
    let lastIndex = tokens.length - 1;
    let lastToken = tokens[lastIndex];

    for (var i in match) {
      let score = checkToken(lastToken, lastIndex, match[i]);
      if (score)
        finalMatch.push(match[i])
    }

    let items = [];

    for (var i in finalMatch) {
      let action = finalMatch[i].actions[lastIndex];
      if (!action) continue;
      if (action.msg) items.push(action.msg)
    }

    myUB.menuHandling.setContent( items );
    console.log(items);
  },

  select: function (id, skipFocus) {

    console.log("selecting", id)
    flow.change({ parent: id })
    DataManager.set("repo.uistate.selected", id)


    if (skipFocus !== "skip")
      $("#order").focus();

    myUB.afterRenderActions.add("scrollIntoViewSelected")
  },

  goToInfo: function (id) {
    DataManager.set("antsView.hudPanel.cfg.antId", id);
    //DataManager.set("workingant.viewState.overPanel.hud", "antsView.hudPanel.nodes")
    this.goToCard("info");
    return false;
  },

  goToCard: function (id) {
    var cards = {
      bridge: "antView.bridge.nodes",
      map: "antView.map.nodes",
      info: "antsView.hudPanel.nodes"
    }

    let path = cards[id];

    if (id == "bridge") {
      myUB.bridgeLoader.loadBridge();
    }

    DataManager.set("workingant.viewState.path", path);

  },

  selectMap: function (id) {
    console.log("selecting map", id);
    myUB.mapview.loadMap(id)
    this.goToCard("map");

    this.select(id, "skip");
  },

  makeSwot: function (name) {
    myUB.shop.run("actionshops.makeSWOTentry", "main", { text: name }, {
      outcome: [(path, value) => {
        hapi.selectMap(value.id)
      }]
    })
  }
}

