DataManager.setCombined("antView.bridge", {
  nodes: {
    loader: {
      get: function (tb, v) {
        tb.fetch({
          items: "bridgeRepo.items",
          root: "bridgeRepo.root",
          repo: "bridgeRepo",
          molds: "antViewBridge.molds"
        }, v);


        this.addVFn(v);

        v.tplSpecs = {
          byLevel: ["root.bridge", "root.card", "root.item"],

        }

        v.brand = {
          borderColor: "#333",
          background: "#eee"
        }

        if (!tb.cache.advanceView) {
          tb.cache.advanceView = { value: ["loading"] };

          myUB.shop.run("actionshops.getRecentPreview", "main", {}, {
            list: [(path, value) => {
              tb.cache.advanceView.value = value;
              DataManager.markChangeOn(tb.path(), false, true);
            }]
          })

        }
        v.actions = cu.store.get(tb, hapi, { runWith: { select: ["selectMap", "antid"] } });


        v.color = "#007abc"
        v.otherTpl = "map.other";
        v.moldsMap = []
        v.defaultTpl = {
          card: "root.card",
          title: "root.title"
        }
        v.cardMold = "map.card";
        v.background = "#eee";
        v.level = 0;
        v.subs = v.root.rels.sub;

        v.id = "root";
        v.item = v.root;
        v.advanceView = tb.cache.advanceView.value;

        let node = this.makeItem(v);

        let style = cu.reactAssembler.run(v.molds.root.style, v)

        return [node, style];
      },

      makeItem: function (v) {
        let moldName = v.mold || v.tplSpecs.byLevel[v.level];
        if (!moldName) console.error("no mold", v);
        let mold = _.get(v.molds, moldName);

        let params = mold.mold.parameters;


        if (params.content) {
          let collection = v.subs[v.id];
          let level = v.level + 1;
          v.content = this.makeList(v, collection, (value, i) => {
            return {
              id: i,
              item: v.items[i],
              level: level
            }
          })
        }


        let node = cu.reactAssembler.run(mold, v)

        return node;
      },


      makeList: function (v, collection, prepare) {
        let list = [];
        v.stack();

        for (var i in collection) {
          v.set(prepare(collection[i], i));
          let node = this.makeItem(v);
          list.push(node);
        }

        v.clean();

        return list;
      },



      addVFn: function (v) {
        v.set = function (value) {
          let v = this;
          let memo = v.memo;
          for (var i in value) {
            if (memo[i] === undefined)
              memo[i] = v[i];
            v[i] = value[i]
          }

        }

        v.get = function (value) {
          let v = this;
          let memo = v.memo;
          for (var i in value) {
            if (memo[i] === undefined)
              memo[i] = v[i];
            v[i] = value[i]
          }
        }

        v.clean = function () {
          let v = this;
          let memo = v.memo;

          for (var i in memo)
            v[i] = memo[i];

          v.memo = v.stacks.pop();
        }

        v.stack = function () {

          let v = this;

          if (!v.stacks) v.stacks = [];

          v.stacks.push(v.memo);

          v.memo = {};
        }


      },


    }
  }
})

