import _ from "lodash"
import faker from "faker"
var cu = window.cu;

var fns = {}

var ub = {

    ctx: {
    },

    doubleCheck: function (id) {
        let tb = fns.evaluate.setup({ data: this.ctx.data, state: this.ctx.state }).target(id);

        for (var i in checks)
            tb.run(checks[i])
    },

    check: function (id) {
        let item = this.ctx.data.items[id];
        let result = this.ctx.data.result[id];
        let shaddow = this.ctx.data.shaddow[id];

        if (!shaddow)
            shaddow = this.ctx.data.shaddow[id] = {}

        let results = this.ctx.data.result;

        let score = shaddow.score = {
            subs: {
                total: 0,
                resolved: [],
                unresolved: []
            },
        };

        for (var slot in item) {
            if (item[slot].id) {
                score.subs.total++;

                if (results[item[slot].id] && results[item[slot].id].isReady)
                    score.subs.resolved.push({ slot: slot, id: item[slot].id });
                else
                    score.subs.unresolved.push({ slot: slot, id: item[slot].id });

            }
        }

        if (result && result.values && result.values.tpl) {
            let tpl = result.values.tpl;
            score.tplParams = { total: 0, defined: 0, resolved: 0, unresolved: [] }
            let parameters = tpl.mold.parameters;

            for (var param in parameters) {
                score.tplParams.total++;
                if (result.values[param] || item[param]) {
                    score.tplParams.defined++;
                }
                else {
                    score.tplParams.unresolved.push(param)
                }
            }
        }

    },

    setup: function (ctx) {
        let item = Object.create(this);

        item.ctx = ctx;

        _.defaultsDeep(ctx, {
            data: {
                items: {},
                result: {},
                shaddow: {},
                trails: {}
            }
        })

        return item;
    }
}



fns.evaluate = {
    target: function (id) {

        let model = this.model || this;

        var item = Object.create(model);

        item.model = model;

        item.setup();

        item.v.targetId = id;

        return item;
    },
    setup: function (opt) {
        this._setup(opt);
        return this;
    },
    _setup: function (opt) {
        if (opt) Object.assign(this, opt)

        if (this.model && this.model.shaft) {
            this.shaft = {};
        }

        this.v = { result: {} };
    },
    get: function (key) {
        if (!_.isString(key)) return key;
        let id = this.v.targetId;

        let fullPath;
        if (key == "values")
            fullPath = ["result", this.v.targetId, "values"]
        if (key == "item")
            fullPath = ["items", this.v.targetId]
        else
            fullPath = [key, "id"];
        return _.get(this.data, fullPath)
    },

    getFull: function (route) {
        let base = route[0];
        let id = route[1];
        let relPath = route[2];

        if (!base) base = "items";
        if (base == "item") base = "items";

        if (!id) id = this.v.targetId;

        let fullPath = [base, id]
        if (relPath) fullPath.push(relPath);

        return _.get(this.data, fullPath.join("."))
    },
    fetch: function (map) {
        for (var i in map) {
            this.v[i] = this.getFull(map[i])
        }
        return this;
    },

    it: function (collection, cb) {
        collection = this.get(collection);

        //let subTb = Object.create( this );
        //subTb.v  = Object.create( this.v )

        //prepare
        let subTb = this;
        let oldValues = {
            slot: subTb.v.slot,
            value: subTb.v.value,
            info: subTb.v.info,
            sub: subTb.v.sub
        }
        subTb.v.slot = null;
        subTb.v.value = null;
        subTb.v.info = null;
        subTb.v.sub = null;
        for (var i in collection) {
            subTb.v.slot = i;
            subTb.v.value = collection[i];
            subTb.v.info = i;
            subTb.v.sub = {};
            cb(subTb, subTb.v)
        }

        //cleanup
        for (var i in oldValues)
            subTb[i] = oldValues[i]

    },

    run: function (cb) {
        try {
            let result = cb(this, this.v)
        } catch (e) {
            if (e && e.type == "notApplicable") {
                return;
            }
            console.log(e)
        }

        if (this.v.result) {

            if (!this.data.trails[this.v.targetId]) {
                this.data.trails[this.v.targetId] = {};
            }
            Object.assign(this.data.trails[this.v.targetId], this.v.result)
        }
    },

    result: function (name, value) {
        this.v.result[name] = value;
    },

    check: function (paths) {
        for (var i in paths) {
            let path = paths[i];

            if (path == "isCurrentItem") {
                let stateCurrentId = this.state.currentItem.targetid;
                if (!stateCurrentId || this.v.targetId != stateCurrentId)
                    throw { type: "notApplicable", path: paths[i] }
            }
            else {
                let value = _.get(this.v, paths[i]);
                if (value === undefined) throw { type: "notApplicable", path: paths[i] }
            }
        }
    }
}

fns.compile = Object.assign({}, fns.evaluate, {
    setup: function (opt) {
        this._setup(opt);

        this.clues = {
            needes: {},
            provides: {},
            collections: []
        }

        return this;
    },
    fetch: function (map) {
        for (var i in map) {
            this.clues.needes[i] = { key: map[i] }
            this.v[i] = { test: "test" }
        }
        return this;
    },


    check: function (check) {

    },

    result: function (name, value) {
        this.clues.provides[name] = value;
    },

    it: function (collection, cb) {
        this.clues.collections.push(collection)
    },

    run: function (cb) {
        let result = cb(this, this.v);
    }
});


var checks = [

    (tb, v) => {

        tb.fetch({
            item: ["item"]
        })

        v.score = {
            total: 0,
            resolved: [],
            unresolved: []
        }

        tb.it(v.item, (tb, v) => {
            let sub = v.value;
            if (!sub.id) return;

            v.score.total++;

            let result = tb.getFull(["result", sub.id]);

            if (!result || !result.isReady) v.score.unresolved.push(sub.id)
            else v.score.resolved.push(sub.id)
        })

        tb.result("subScore", v.score)
        return { subScore: v.score }
    },



    (tb, v) => {

        tb.fetch({
            values: ["result", "", "values"],
            item: ["item"],
            parameters: ["result", "", "values.tpl.mold.parameters"]
        })

        tb.check([
            "values.tpl"
        ])

        v.score = { total: 0, defined: 0, undefined: [] }
        v.params = {};
        tb.it(v.parameters, (tb, v) => {
            let param = v.slot;
            v.params[param] = true;
            v.score.total++;
            if (!v.item[param] && !v.values[param])
                v.score.undefined.push(param)
        })

        tb.it(v.parameters, (tb, v) => {
            let param = v.slot;
            v.params[param] = true;
        })


        v.score.undefinedCount = v.score.undefined.length;
        v.score.definedCount = v.score.total - v.score.undefinedCount;

        tb.result("tplParamsScore", v.score);
        tb.result("tplParams", v.params)
    },



    (tb, v) => {

        tb.fetch(  {
            trails : ["trails"],

        })
        tb.check([
            "isCurrentItem"
        ])

        console.log("trails is currentItem", v.trails);
    }

]

