import React, { Component } from 'react';
import _ from "lodash";
import $ from "jquery";
import go from "gojs";
import "../libs/dataManager";
var DataManager = window.DataManager;


var KeyboardManager = {
    setup: function () {
        if (this.isInitialized) return;

        $("body").on("keydown", (evt) => {
            this.handleKey(evt)
        })

        DataManager.setTrigger("keyboardShortcuts.layout", "kbdManager", {
            run: () => {
                this.load();
            }
        })
        this.load();
    },

    load: function () {
        let layout = DataManager.getValue("keyboardShortcuts.layout");
        this.layout = layout || {};
    },

    handleKey: function (evt) {
        var name = [];
        if (evt.altKey) name.push("alt");
        if (evt.ctrlKey) name.push("ctrl");
        if (evt.metaKey) name.push("meta");
        if (evt.shiftKey) name.push("shift");

        name.push(evt.key.toLowerCase())
        let comb = name.join("+");

        if (this.layout[comb])
            return this.run(this.layout[comb], comb)
    },

    run: function (action,comb) {
        if (_.isFunction(action)) return action(comb, action);
        else if (action.run)
            return action.run(comb, action)
    }
}

KeyboardManager.setup();

export default KeyboardManager