import _ from "lodash"
import faker from "faker"
import $ from "jquery"
var cu = window.cu;



var ub = {

    ctx: {
        nextUID : 0
    },

    createStore : function ( tb, hapi, fns  ) {
        if (tb && tb.cache.store ) return tb.cache.store;

        let tpls = {
          changeValueTpl : ( path ) =>  {
            return function ( evt ) {
              let value = evt.target.value;
              hapi.set( path, value )
            }
          }, 
          runActionTpl : ( action ) => {
            return function ( evt ) {
              if ( hapi[action] ) return hapi[action]( evt );
            }
          },
          runWithActionTpl : ( action ) => {
            return function ( evt ) {
                let target = evt.target;
                let attr = $(target).attr( action[1])
                if ( attr === undefined ) {
                    target = $(target).parents("[" + action[1] + "]")
                    attr = $(target[0]).attr( action[1])

                }

              evt.stopPropagation();
              if ( hapi[action[0]] ) return hapi[action[0]]( attr, evt );
            }
          },

          runWithParam : ( action ) => {
            return function ( param ) {
            if ( hapi[action] ) return hapi[action]( param );
          }          }

        }


        let store = {}
  
        for ( var i in fns.change ) {
          store[i]  = tpls.changeValueTpl( fns.change[ i ])
        }
  
  
        for ( var i in fns.run ) {
            store[i]  = tpls.runActionTpl( fns.run[ i ])
          }

          
            
        for ( var i in fns.runWith ) {
          store[i]  = tpls.runWithActionTpl( fns.runWith[ i ])
        }



        for ( var i in fns.runWithParam ) {
          store[i]  = tpls.runWithParam( fns.runWithParam[ i ])
        }

        if ( tb )
          tb.cache.store = store;
        return store;
      }

}

