import _ from "lodash"
import faker from "faker"
var cu = window.cu;



var ub = {

    ctx: {
        payload: {
            data: {

                items: {},
                subs: {},
            },

            src: {
                getItem: function (id) {

                },

                getSubs: function (id) {

                }
            },

            tpls: [],
            registerTpl: function (args, master) {
                this.tpls.push(args.path);
            },

            cases: [
                result => {
                    let isApplicable = !result.obj.item;

                    if (!isApplicable) return;

                    let id = result.obj.id;
                    let item = ub.ctx.payload.data.items[id]

                    if (!item) {
                        item = {
                            text: faker.company.catchPhrase(),
                            id: id
                        }

                        if (ub.ctx.payload.setter)
                            ub.ctx.payload.setter(["items", id], _.cloneDeep(item) )
                    }


                    result.obj.item = item;
                    return "applied";
                },

                result => {
                    let isApplicable = !result.obj.subs && result.obj.item && result.obj.level < 4;
                    if (!isApplicable) return;

                    let obj = result.obj;

                    let id = result.obj.id;

                    let subs = ub.ctx.payload.data.subs[id];

                    if (!subs && !ub.ctx.payload.generated) {
                        subs = {};

                        let count = 4;
                        for (var i = 0; i < count; i++) {
                            let id = faker.random.uuid();
                            let tpl = null;
                            if (obj.level == 1) tpl = faker.random.arrayElement([null, "columns"]);
                            let sub = {
                                id: id,
                                level: result.obj.level + 1,
                                tpl: tpl
                            }

                            subs[id] = sub;
                        }

                        if (ub.ctx.payload.setter)
                            ub.ctx.payload.setter(["subs", id], _.cloneDeep( subs ) )

                    }

                    if ( !subs ) subs = {}

                    result.obj.subs = subs;
                    result.subs = subs;

                    return "applied";
                }
            ]
        },

        tb: {
            //parseHtml: cu.parseStringIntoComp
        }
    },

    run: function (obj, attrs) {
        let oldPayload = this.ctx.payload;

        this.ctx.payload.attrs = attrs || [];

        let mold = this.it(obj, []);

        this.ctx.payload = oldPayload;

        return mold;
    },


    it: function (obj, path) {
        let oldPayload = null;
        let result = {
            oldPayload: null,

            obj: obj,
            sub: obj,

            master: this,
            path: path,
            values: {},
            ops: []
        }

        this.prepare(result);

        for (var i in result.subs) {
            let ret = this.it(result.subs[i], path.concat([i]));
            result.values[i] = ret;
        }

        return this.integrate(result)
    },

    addPayload: function (result, obj) {
        if (!result.oldPayload) {
            result.oldPayload = this.ctx.payload;
            this.ctx.payload = Object.create(this.ctx.payload);
        }
        Object.assign(this.ctx.payload, obj);
    },

    prepare: function (result) {

        let cases = this.ctx.payload.cases;

        let hasChanges = true;
        let applied = false;
        let count = 0;
        while (hasChanges && count < 5) {
            hasChanges = false;
            count++;
            for (var i in cases) {
                let _case = cases[i];
                let ret = _case(result);
                if (ret == "applied") {
                    hasChanges = true;
                    applied = true;
                    break;
                }
            }
        }
    },

    integrate: function (result) {
        if (result.ops.length) {
            for (var i in result.ops) {
                let op = result.ops[i];
                this.ctx.payload[op.name](op.args, this)
            }
        };

        if (result.oldPayload) this.ctx.payload = result.oldPayload;

        return result.obj;
    }
}

var inputs = {
    items: { level: 0 },
    subs: {},
}
var test = (result) => {
    let value = ub.run(inputs.items);

    result.x = value;


    cu.tests.pin("testRepo", value)
    //result.mold = value;
}

cu.testDataGenerator = {
    //test,

    run: function (id, data, generated,  setter) {

        ub.ctx.payload.data = data;
        ub.ctx.payload.setter = setter;
        ub.ctx.payload.generated = generated;

        return ub.run({ id: id || "root", level: 0 }, [])
    },

    init: function () {
        ub.ctx.tb.parser = cu.tplObjParse;
        ub.ctx.tb.reactAssembler = cu.reactAssembler;
    },

    def: `
    `,

    debug: {
        ub: ub
    }
}