import _ from "lodash"
import { UV_UDP_REUSEADDR } from "constants";
var cu = window.cu;


var ub = {

    ctx: {
        payload: {
        },

        tb: {
        }
    },

    run: function (obj) {
        let oldPayload = this.ctx.payload;

        let mold = {
            ops: [],
            parameters: {},
            pins: {},
            tpl: {},
            comments : []
        }

        mold.tpl = this.getTpl(obj, [], mold)
        this.ctx.payload = oldPayload;

        return mold;
    },

    getTpl: function (obj, path, mold) {
        if (_.isFunction(obj)) {
            let str = obj.toString();
            let result = this.opValues(obj.toString(), path, mold, "makeFunction");
            if (str == result) return obj;
            return result;
        }
        else if (_.isObject(obj)) {
            let newValue = {};
            for (var i in obj) {
                let slot = i;
                let value;
                if ( slot.indexOf( "{") != -1 || slot.indexOf( ".") != -1 ) {
                    let x = {
                        VALUE : obj[ i ], 
                        PATH  : i
                    }

                    slot = slot.replace(/[^\w]+/g, "_" );
                    value = this.getTpl(x, path.concat(slot), mold);

                    this.addOp( mold, path.concat(slot), { type : "pathSet", stage : 20, path : path.concat(slot)  }  )
                }
                else 
                    value = this.getTpl(obj[i], path.concat(i), mold)

                newValue[slot] = value;
            }
            return newValue;
        }
        else if (_.isArray(obj)) {
            let newValue = [];
            for (var i=0; i<obj.length; i++ )
                newValue.push(this.getTpl(obj[i], path.concat(i), mold));
            return newValue;
        }
        else if (_.isString(obj)) {
            return this.opValues(obj, path, mold, "concat")
        }
        else return obj;
    },

    addOp: function (payload, path, op) {
        if (!op.stage) op.stage = 0;
        if (op.action)
            payload.parameters[op.action] = op.path;
        payload.ops.push(op)
    },
    addComment: function (payload, path, text) {
        let comment = { text : text, path : path }
        payload.comments.push(comment)
    },

    opValues: function (value, path, mold, finalOp) {
        if (!_.isString(value)) return value;

        /*
        let comments = value.matchAll(/\/\/\.\:.+^/g);
        for ( var i in comments )
            this.addComment( mold, path, comments[i])
        */

        if (value.indexOf("{") == -1) return value;


        let ops = mold.ops;
        let values = value.split(/(\{[^}\n\:\;]+)\}|(\$\$\w+)/g);
        values = values.map((item, i) => {
            if ( !item ) return null;

            let firstChar = item.charAt(0);

            if ( firstChar == "{") {
                item = item.substring(1);
            }
            else if ( firstChar == "$") {
                item = item.substring( 2 );
            }
            else return item;

            let action = item;
            return { type: "value", action , firstChar  };
        })
            .filter(item => item)


        if (values.length == 0) return null;
        else if (values.length == 1) {
            values = values[0];
            if (values.type) {
                values.path = path;
            }
            if (_.isObject(values))
                this.addOp(mold, values.path, values)
            return values;
        }
        else if (!finalOp) {
            values = values.map((item, i) => {
                if (item.type) {
                    item.path = path.concat(i);
                    item.index = i;
                    item.stage = 0;
                    this.addOp(mold, path, item);

                    return item;
                }
                else return item;
            })
            return values;
        }
        else {
            values = values.map((item, i) => {
                if (item.type) {
                    item.path = path.concat(i);
                    item.index = i;
                    item.concatParent = true;
                    item.stage = 0;

                    this.addOp(mold, path, item);
                    return item;
                }
                else return item;
            })
            this.addOp(mold, path, { path: path, type: finalOp, ops: values, stage: 1 });
            return values;
        }

    }
}

