
import React, { Component } from 'react';
import { UnControlled as CodeMirror } from 'react-codemirror2'
import 'codemirror/lib/codemirror.css';
require('codemirror/mode/javascript/javascript');

class CodeMirrorEditor extends Component {

  constructor(props) {
    super(props);

    this.state = {};

    this.changeValue = this.changeValue.bind(this);
    this.changeCursor = this.changeCursor.bind(this);
    this.isBlured = this.isBlured.bind(this);
  }

  changeValue(editor, data, value) {

    if (!this.props || !this.props.valueChanged)
      return;

    //window.editor = editor;


    let cursor = editor.getCursor();
    if (data.origin != undefined) {
      this.props.valueChanged({ scode: value, cursor: { line: cursor.line, ch: cursor.ch } });
    }
    else {
      this.props.valueChanged({ scode: value })
    }

    this.setState({ caret: editor.cursorCoords(true, "local") })
  }

  changeCursor(editor, cursor) {
    this.setState({ caret: editor.cursorCoords(true, "local") })

    if (cursor.sticky == undefined) return;

    if (!this.props || !this.props.valueChanged)
      return;

    this.props.valueChanged({ cursor: { line: cursor.line, ch: cursor.ch } });

  }
  isBlured() {
    if (this.props.blured)
      this.props.blured()
  }

  render() {

    //this means that setting the value with null will delete the value from wallet ?!?
    let text = this.props.data.scode || "";
    if (!text.length) text = "";

    let cursor = this.props.data.cursor;
    if (!cursor || !cursor.ch) cursor = 0;

    let popup;
    let caret = this.state.caret || { right : 0, bottom : 0} 
    if (caret) {

      let left = caret.right + 30;
      let top = caret.bottom + 0;

      popup = <div className="popup" style={{ transform: "translate(" + left + "px ," + top + "px)", zIndex: 100 }} > Salut  </div>
    }
    else popup = null;

    let style = `
      .popup { border : 1px solid #999; background : #bbb; padding : 10px; min-width:40px; width : 100px; transition : all 0.2s ease; }

      .popup { opacity : 0.05; position : absolute;pointer-events: none;}
    `
    return <div style={{position:"relative"}}>
      <style>{style}</style>
      {popup}
      <CodeMirror
        value={text}

        options={{
          mode: 'javascript',
          lineNumbers: true, 
          smartIndent : true,
        }}

        autoCursor={false}

        cursor={cursor}
        ///onBeforeChange={this.changeValue}
        onChange={this.changeValue}
        onCursor={this.changeCursor}
        onBlur={this.isBlured}
      />
    </div>
  }
}

export default CodeMirrorEditor;